.post {
    display: grid;
    grid-template-areas:
            "h h h"
            "d tc tc"
            ". f f";
    grid-template-rows: 2em 1fr 2em;
    grid-template-columns: .3fr 1fr .5fr;
    background-color: #44475a;
    margin: 0 0 10px 0;
    padding: 0 15px 0 15px;
    border-radius: 7px;
}

.header {
    padding: .25em 0 .25em 0;
    grid-area: h;
    text-align: right;
    border-bottom: 3px solid #282a36;
    font-weight: bolder;
    color: #949492;
}

.descr {
    grid-area: d;
    padding: 0;
    margin: 0;
    border-right: 1px solid #282a36;
    border-bottom: 2px solid #282a36;
}

.textContent {
    grid-area: tc;
    padding-left: 15px;
    margin: 0;
    border-bottom: 2px solid #282a36;
}

.post ul {
    padding: 0;
    margin: 0;
}

.post li {
    display: block;
    list-style-type: none;
    padding: 10px 0 10px 0;
    text-align: center;
    font-weight: bold;
}

.roundImg {
    width: 72px;
    height: 72px;
    border-radius: 50%;
}

.footer {
    grid-area: f;
    display: flex;
    justify-content: flex-end;
    color: #949492;
}

.postButtons {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.postButtons div {
    text-align: center;
    width: 150px;
}